.footerCont {
  display: flex;
  flex-direction: row;
  background: hsl(260, 8%, 14%);
  gap: 5rem;
}

.footerCont h5 {
  max-width: 5rem;
  height: 3rem;
  margin: 3rem 10rem 0 4rem;
  margin-right: 10rem;
  color: #fff;
}

.footerLinks {
  display: flex;
  flex-direction: column;
  margin-top: 3.5rem;
  gap: 1rem;
}

.footerList {
  display: flex;
  flex-direction: column;
  gap: 1rem;
}

.socialMedia {
  display: flex;
  margin-top: 3.5rem;
  margin-left: 2rem;
  gap: 2rem;
}
.socialMedia img {
  max-width: 2rem;
  height: 2rem;
  cursor: pointer;
}

@media screen and (max-width: 1180px) {
  .footerCont {
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 1rem;
    padding-top: 1rem;
  }

  .footerCont h5 {
    margin: 0;
  }

  .footerLinks {
    margin: 0;
    align-items: center;
  }
  .socialMedia {
    margin-top: 0;
    margin-bottom: 2rem;
  }
}
